@-webkit-keyframes ripple{
  0%{
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
  }
  
  50%{
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }
  
  100%{
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
  }
}

@keyframes ripple{
  0%{
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
  }
  
  50%{
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }
  
  100%{
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
  }
}




